<template>
	<view class="detail">
		<view style="width: 100%; height: 52rpx; background-color: #000;"></view>
		<view class="header">
			<view @click="bback">
				<image style="width: 40rpx; height: 40rpx; margin-top: 20rpx; margin-left: 20rpx;  " src="@/static/img/arrow-left-bold1.png"></image>
			</view>
			<view style="font-size: 38rpx; margin-left: 10upx;">详情</view>	
		</view>	
		<view class="jpkc">
			<view class="jpkcLeft">
				<view class="jpkcImg">
					<image style="width:180rpx; height:180rpx" :src="infos.logo"></image>
				</view>
				<view class="jpkczi">
					<p style=" font-weight: bold;">{{infos.title}}</p>
							
					<view class="jpkcBottom">
						<view style="color: #e94466; font-size: 34rpx; font-weight: bold;">￥0</view>
			
						<view style="margin-top: 12rpx;" class="jpkcPrice">
							<view class="progress-box">
								<view style="margin-bottom: 20rpx;">
									该夺宝需{{infos.all_user}}次参加
								</view>
								
								<!-- <progress style="margin-top: 10rpx;" activeColor="#fb2e59" font-size="12" border-radius="50"  :percent="pgList[0]" show-info="true" stroke-width="10" /> -->
								<!-- <liu-progressbar :progress="pgList[0]" dsColor="#dce1e7"  color="#333" borderRadius="50rpx" bgColor="#fb2e59" :height="'30rpx'" />-->
								<ProgressBar :Width="bfb" Type="candy"></ProgressBar>
								<view style="margin-top: 20rpx;">
									还差{{sheng}}次开奖
								</view>
	
							</view>										
		
						</view>
					</view>
					
				</view>
			</view>	
		</view>
		<view class="ddb">
			<view class="ddbLeft">
				<view style="font-size: 30rpx;">夺宝战况</view>
				<view style="font-size: 28rpx;">本期参加夺宝{{infos.now_user}}次</view>
				<view style="color: #8e8e8e; font-size: 24rpx;">点击查看往期战况</view>
				<view class="dck" @click="dbzkk">点击查看</view>
			</view>
<!-- 			<view class="ddbRight">
				<view style="font-size: 30rpx;">夺宝晒圈</view>
				<view style="font-size: 28rpx;">共有0次发布动态</view>
		
				<view class="dck" >点击查看</view>				
			</view> -->
		</view>
		<view class="ffs" @click="kj">
			<view>开奖方式</view>
			<view>当夺宝人数满{{infos.all_user}}次时
				<image style="width: 16rpx; height: 28rpx; margin-left: 10rpx;"  src="../../static/img/right.png"></image>
			</view>
		</view>
		<view style="width: 90%; margin: 0 auto; font-size: 28rpx; line-height: 40rpx;">
			每个商品设置一定的抽奖人数，粉丝每看一次视频广告，可获得一个抽奖码，当抽奖码个数等于抽奖人数时，系统自动开奖，从所有抽奖码中随机一个中奖，系统会向中奖用户发送模版消息，当前期结束。兑奖时间:自中奖之日起30日之内，超过兑奖时间视为自动放弃
		</view>
		<view class="footer">
			<view class="fbbn">首页</view>
			<view class="fbbn">客服</view>
			<view class="fbbn">分享</view>
			<!-- <view class="joinn" @click="dbInfo">再次参加</view> -->
			<view class="dddd" @click="dbmm">我的夺宝码</view>
		</view>
	</view>
</template>

<script>
	import ProgressBar from '../../components/Progress-Bar.vue'
	export default {
		data() {
			return {
				idd:"",
				infos:[],
				sheng:"",
				bfb:""
			}
		},
		components: {
			 ProgressBar
		//	 cmdProgress
		},
		onLoad(options) {
			//this.cydb()
			console.log("opp")
			console.log(options)
			this.idd = options.id
			console.log(this.idd)
		},
		onShow(){
		//	this.dbInfo()
			this.ddInfo()
		},
		methods: {
			ddInfo(){
				uni.request({
					method:"POST",
					url:this.apiUrl+"index.php/index/works",
					data:JSON.stringify({
					//	"openid":uni.getStorageSync("openid"),
						"id":this.idd
					}),
					success: (res) => {
						console.log("rres")
						console.log(res)
						this.infos=res.data
						this.sheng=this.infos.all_user - this.infos.now_user
						console.log(this.sheng)
						this.bfb=((this.infos.now_user / this.infos.all_user)*100).toFixed(2)
					}
				})				
			},
			kj(){
				uni.showModal({
					title: '开奖方式说明',
					content: '每个商品设置一定的抽奖人数，粉丝每看一次视频广告，可获得一个抽奖码，当抽奖码个数等于抽奖人数时，系统自动开奖，从所有抽奖码中随机一个中奖，系统会向中奖用户发送模版消息，当前期结束。兑奖时间:自中奖之日起30日之内，超过兑奖时间视为自动放弃。',
					showCancel:false,
					success: function (res) {
						if (res.confirm) {
							console.log('用户点击确定');
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			},
			dbInfo(){
				uni.request({
					method:"POST",
					url:this.apiUrl+"index.php/index/cydb",
					data:JSON.stringify({
						"openid":uni.getStorageSync("openid"),
						"dbid":this.idd
					}),
					success: (res) => {
						console.log(res)
						if(res.data.status==1){
							uni.showToast({
								title:res.data.data,
								duration:2000
							})						
						}
						if(res.data.status==-1){
							uni.showToast({
								icon:"error",
								title:res.data.data,
								duration:2000
							})						
						}

						this.ddInfo()
					}
				})				
			},
			bback(){
				uni.navigateBack(-1)
			},	
			dbzkk(){
				uni.navigateTo({
					url:"/pages/dbzk/dbzk?id="+this.idd
				})
			},
			dbmm(){
				uni.navigateTo({
					url:"/pages/dbm/dbm?id="+this.idd+"&bfb="+this.bfb
				})
				// uni.request({
				// 	url:this.apiUrl+""
				// })
			}
		}
	}
</script>

